<template>
<section class="wrap has_header_wrap c_wrap">
	<!-- 头部标题 -->
	<mt-header fixed title="物流详情">
      <mt-button icon="back" slot="left" @click.prevent="goBack"></mt-button>
	</mt-header>

	<!-- 物流信息 -->
	<div class="info d_flex">
		<div class="info_img">
			<!-- <img src="../../../static/images/proimg.jpg" alt=""> -->
			<img v-lazy="curOrderList.productUrl" :alt="curOrderList.orderDetails.productName">
		</div>
		<div class="left d_flex">
			<p>物流状态：<span class="e_state">{{ curOrderList.orderStatus | orderStatusFilter }}</span></p>
			<p>承运公司：<span>{{ curOrderList.logistics?curOrderList.logistics.logisticsName:'' }}</span></p>
			<p>运单编号：<span>{{ curOrderList.logisticsNumber }}</span></p>
			<p>官方电话：<span>{{ curOrderList.logistics?curOrderList.logistics.phone:'' }}</span></p>
		</div>
	</div>
	<!-- 菜鸟裹裹 -->
	<mt-cell class="cngg" is-link >
	  <img slot="icon" src="../../../static/images/cngg.png">
	  <span slot="title">本数据由<span>菜鸟裹裹</span>提供</span>
	</mt-cell>
	<!-- 菜鸟裹裹 end -->

	<!-- 头部标题 end -->
	<section class='wrap_in'>
		<!-- 详细物流 -->
		<div class="exp_detail d_flex">
			<div class="line">
				<!-- <img src="../../../static/images/progress-1.png" alt="">
				<img src="../../../static/images/progress-0.png" alt=""> -->
			</div>
			<ul>
				<li v-for="(el,index) in expressList">
					<p>【{{ el.address }}】{{ el.remark }}</p>
  					<span>{{ el.time }}</span>
  				</li>
				<!-- <li>
					<p>【温州市】温州市中转部已发出</p>
  					<span>2018-08-21   19:44:34</span>
  				</li> -->
			</ul>
		</div>
		<!-- 详细物流 end -->
		<!-- 物流信息 end -->
	</section>
</section>
</template>
<script>
import { Header, Button, Cell, Toast } from 'mint-ui';

export default {
	name: 'order_detail',
	data (){
		return {
			mtCell: '本数据由<span>菜鸟裹裹</span>提供',
			expressList: [],//物流信息
			// code: this.$route.query.code,// 快递编号
			// lname: this.$route.query.lname,// 快递名称
			// lphone: this.$route.query.lphone,// 快递号码
			// status: this.$route.query.status,// 物流状态
			curOrderList: this.$store.state.orderInfo?JSON.parse(this.$store.state.orderInfo):'',//当前订单信息
		}
	},
	// props: ['curOrderList'],
	components: {
		'mt-header': Header,
		'mt-button': Button,
		'mt-cell': Cell,
	},
	filters: {
		//订单状态过滤
		orderStatusFilter ( data ){
			// 1.在线未支付    2.货到付款待确认    3.待发货    4.待收货    5.物流待收款    6.财务待收款    7.已完成    8.已取消    9.支付中
			if( data == 1 || data == 9 ){ return '待付款' }
			else if( data == 3 || data == 2 ){ return '待发货' }
			else if( data == 4 ){ return '待收货' }
			else if( data == 5 || data == 6 || data == 7 ){ return '已送达' }
			else if( data == 8 ){ return '已取消' }
		},
	},
	mounted (){
		this.$emit('changeClass',false);
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	created(){
		const _self = this;
		// console.log('物流: ', _self.curOrderList)

		if( !this.curOrderList.logistics ){return}
		//获取物流信息
		this.$ajax({
			url: _self.webRoot + '/delivery/getLogistics',
			data: {
				logisticsNo: _self.curOrderList.logisticsNumber,//物流单号
				logisticsCode: _self.curOrderList.logistics.logisticsCode,//物流公司编码
			}

		})
		.then(function(res){
			// console.log('物流信息: ', res)
			if( res.data.success ){
				if( res.data.data.length === 0 ){
					_self.$options.methods.showToast('暂无快递信息!');
				}
				for( var i = 0; i < res.data.data.length; i++ ){
					_self.expressList.unshift( res.data.data[i] );
				}
			}
		})
		.catch(function(err){
			console.log(err)
		});
	},
	methods: {
		//显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
		// 后退
		goBack (){	
			this.$router.go(-1);
		},
		//确定按钮
		btnSave (){	
			console.log('点击确定')
		},
	},
}	
</script>

<style scoped>
	@import '../../../static/css/headerTitle.css';	
	@import './Css/express.css';	
</style>
<style>
.cngg .mint-cell-wrapper {
	background: none;
}
.cngg .mint-cell-title {
	font-size: 0 ;
}
</style>


